.topbar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: white;
    padding: 8px;
    margin-bottom: 16px;
    box-shadow: 0px 0px 5px;
}

.topbar>* {
    margin: 5px;
}

.search {
    width: 250px;
    border-radius: 50px;
    background-color: #F0F2F5;
    border: 0;

    display: flex;
    justify-content: flex-end;
}

.purple-circle {
    width: 45px;
    /* Circle size */
    height: 45px;
    /* Circle size */
    border-radius: 50%;
    /* Make it round */
    background-color: #9e27df;
    /* Circle color */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    margin-top: 8px;
    margin-left: 10px;
    /* For positioning the scrollable list correctly */
}

.white-circle {
    width: 45px;
    /* Circle size */
    height: 45px;
    /* Circle size */
    border-radius: 50%;
    /* Make it round */
    background-color: #F0F2F5;
    /* Circle color */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    margin-top: 8px;
    margin-left: 10px;

    /* For positioning the scrollable list correctly */
}
.purple-circle-img{
    width: 53px;
    height: 30px;
    position: absolute;
   
}
.white-circle-img{
    width: 53px;
    height: 30px;
    position: absolute;
   
}

.red {
    background-color: #975abb;
}

.search>input {
    background-color: transparent;
    border: 0 none;
    font-size: 14px;
    flex-grow: 5;
}

.search>input:focus {
    outline: none;
}

.search>svg {
    flex-grow: 1;
    margin-left: 10px;
    margin-right: 5px;
}

.clickable {
    cursor: pointer;
}

.dark-mode-btn {
    margin-left: auto;
    margin-right: 20px;
    height: 25px;
    margin-top: 17px;
    border-radius: 8px;
    background-color: #771dac;
    border-color: #771dac;
    color: white;
    font-size: 1vw;
    white-space: normal; /* Allow text wrapping if necessary */
    overflow: hidden; /* Prevent overflow */
    text-overflow: ellipsis; /* Use ellipsis for overflow text */
    display: inline-block; /* Ensure wrapping applies */
    word-wrap: break-word; 
}

.dark-mode-btn:hover {
    cursor: pointer;
    background-color: #580c84;
}

.bi-bi-facebook:hover{
    cursor:pointer;
}