.notification-popout {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(111, 105, 105, 0.5);
    /* Semi-transparent background */
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    justify-content: center;
    /* Center the modal horizontally */
    align-items: center;

}