@media (min-width: 1101px) {
    .show-sm {
        display: none;
    }
}

@media (min-width: 1301px) {
    .show-md {
        display: none;
    }
}

@media (max-width: 1100px) {
    .hide-sm {
        display: none;
    }

    .sm {
        justify-content: center !important;
    }
}

@media (max-width: 1300px) {
    .hide-md {
        display: none;
    }
}