.wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.container {
    display: flex;
    justify-content: space-between;
    margin-left: 2%;
    margin-right: 2%;
}

.home {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #F4FDF9;
    overflow: hidden;
    min-height: 100vh;
}

.dark-mode {
    background-color: #333738;
    color: #ffffff;
}

.dark-mode .post {
    background-color: #252525;
    color: #ffffff;
}

.dark-mode .topbar {
    background-color: #252525;
    color: #ffffff;
}

.dark-mode .add-post-intro {
    background-color: #252525;
    color: #ffffff;
}



.dark-mode .hidden {
    background-color: transparent;
    color: transparent;
}
.dark-mode .menu {
    background-color: #333738;
    color: #ffffff;
}

.dark-mode .side-item {
    color: #ffffff;
}

.dark-mode .post-author {
    color: #ffffff;
}

.dark-mode .comment {
    background-color: #333738;
    color: #ffffff;
}

.dark-mode .edit-comment-state {
    background-color: #333738;
    color: #ffffff;
}

.dark-mode .modal-content {
    background-color: #333738;
    color: #ffffff;
}

.dark-mode .likes-counter {

    color: #ffffff;
}

.dark-mode .comments-counter {

    color: #ffffff;
}
.dark-mode .likeButton:hover{
    background-color: #333738;
}
.dark-mode .shareButton:hover{
    background-color: #333738;
}
.dark-mode .commentButton:hover{
    background-color: #333738;
}
.dark-mode .edit-post-button:hover{
    background-color: #333738;
}
.dark-mode .delete-post-button{
    background-color: transparent;  
    color: white;
}
.dark-mode .delete-post-button:hover{
    background-color: #333738;
}
.dark-mode .delete-comment-button:hover{
    background-color: #333738;
}
.dark-mode .delete-comment-button{
    background-color: transparent;  
    color: white;
}
.dark-mode .edit-comment-button:hover{
    background-color: #333738;
}
.dark-mode .side-item:hover{
    background-color: #252829;
}
.dark-mode .scrollable-list{
    background-color: #333738;
}

body {
    margin: 0px !important;
}