.post {
    background-color: #fff;
    border: 1px solid #dddfe2;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 30px;
    width: 700px;

}

.post-header {
    display: flex;
    align-items: column;

    margin-bottom: 10px;
    justify-content: space-between;

}

.post-author-detials {
    display: flex;
    align-items: column;
    font-weight: bold;

}

.post-avatar {
    border-radius: 50%;
    height: 40px;
    width: 40px;
    object-fit: cover;
    box-shadow: 0px 0px 3px 0px #000000;
    margin-left: 2px;
    margin-top: 2px;
    margin-right: 10px;
}

.post-author {
    font-weight: 600;
    color: #050505;
    font-weight: bold;
    font-size: 16px;
}

.post-date {
    color: #65676b;
    font-size: 13px;
    margin-top: 4px;
}

.post-content {
    margin-bottom: 12px;
    display: flex;
    margin-left: 10px;
}

.post-actions {
    display: flex;
    align-items: center;
}

.post-action {
    margin-right: 16px;
    color: #65676b;
    font-size: 14px;
    cursor: pointer;
}

.post-action:hover {
    color: #1877f2;
}

.post-action-icon {
    margin-right: 4px;
}
.post-image-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    max-height: 500px;
    max-width: 100%;

}

.post-image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    max-height: 500px;
    max-width: 100%;
}

.post-bottom-icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #dddfe2;
    width: 700px;
    max-height: 80px;
    margin-bottom: 10px;

}

.likeIcon {
    width: 65px;
    height: 25px;
    margin-top: 3px;
    cursor: pointer;
    margin-right: 10px;
}

.shareIcon {
    width: 70px;
    height: 27px;
    margin-top: 11px;
    cursor: pointer;
    scroll-margin-left: 10px;
}

.commentIcon {
    width: 90px;
    height: 25px;
    margin-top: 8px;
    cursor: pointer;

}

.post-bottom-counters {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #dddfe2;
    width: 700px;
    max-height: 80px;

}

.like {
    width: 17px;
    height: 15px;
    margin-top: 8px;
    margin-right: 5px;
    margin-left: 5px;
}

.comments-counter {
    border-color: transparent;
    margin-top: 5px;
    margin-left: 5px;
    box-shadow: none;
    background-color: transparent;
    cursor: pointer;
}

.hidden {
    display: none;
    /* Hide the element */
    pointer-events: none;
    /* Make the element not clickable */
}

.likes-counter {
    border-color: transparent;
    margin-bottom: 5px;
    margin-top: 5px;
    box-shadow: none;
    background-color: transparent;
    cursor: pointer;

}

.liked {
    color: #1877f2;
}

.commentButton {
    background-color: transparent;
    border: none;
    height: 50px;
}

.likeButton {
    background-color: transparent;
    border: none;
    margin-right: 40px;
    height: 50px;
}

.shareButton {
    background-color: transparent;
    border: none;
    margin-left: 40px;
    height: 50px;
  
}

.commentButton:hover {
    background-color: #f0f0f0;
    height: 50px;
}

.likeButton:hover {
    background-color: #f0f0f0;
    height: 50px;
}

.shareButton:hover {
    background-color: #f0f0f0;
    height: 50px;
}

.comments-list {
    list-style-type: none;
}

.comment {
    background-color: #f0f0f0;
    border-radius: 15px;
    padding: 20px;
    margin-bottom: 10px;
    display: table;

}

.comments-list li {
    display: flex;
    align-items: center;

}

.comment-avatar {
    border-radius: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-shadow: 0px 0px 3px 0px #000000;
    margin-right: 10px;

}

textarea::placeholder {
    font-family: Arial;
    font-size: 14px;
}

.share-menu {
    display: flex;
    flex-direction: column;
}

.post-bottom-left {
    position: relative;
}

.dropdown-share-menu {
    position: absolute;
    top: 40px;
    /* Adjust this value based on your layout */
    left: 0;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 5px;
    margin-top: 10px;
    width: 200px;
}

.share-menu-item {
    background-color: white;
    border: none;
    border-radius: 5px;
    text-align: left;
    font-size: 16px;

}

.share-menu-item:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}

.edit-post-button {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    background-color: transparent;
    border: none;
    border-radius: 5px;
}

.edit-post-button:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}

.delete-post-button:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}

.delete-post-button {
    width: 20px;
    height: 20px;
    font-size: 26px;
    color: #444547;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    background-color: white;
    border: none;
    border-radius: 5px;
    flex-direction: column;
}

.edit-Icon {
    width: 18px;
    height: 18px;

}

.post-edit-delete {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-items: flex-start;
}

.delete-comment-button {
    width: 14px;
    height: 7px;
    font-size: 20px;
    color: #444547;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: none;
    border-radius: 5px;
    flex-direction: column;
}

.edit-comment-button {
    width: 7px;
    height: 7px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    background-color: transparent;
    border: none;
    border-radius: 5px;
}

.edit-comment-state {
    display: flex;
    align-items: center;
    margin-top: 3px;
    background-color: #f0f0f0;
    border-radius: 8px;


}

.edit-icon-comment {
    margin-top: 1px;
    width: 15px;
    height: 15px;

}

.comment-header {
    display: flex;
    align-items: column;
    margin-bottom: 5px;
    justify-content: space-between;
    font-weight: bold;
}

.edit-comment-button:hover {

    cursor: pointer;
}

.delete-comment-button:hover {

    cursor: pointer;
}

.textarea-container {
    flex: 1;
    /* Grow to fill available space */
}

.comment-edit-delete-btn {
    margin-left: 20px;
    display: flex;
}

.send-icon {
    width: 20px;
    height: 20px;
}

.send-changes-comment-btn {
 
    background-color: #580c84;
    color: white;
    width: 60px;
    height: 20px;
    align-self: flex-end;
    border-radius: 8px;
    align-items: center;
    margin-top: 7px;
}

.send-changes-comment-btn:hover {
    cursor: pointer;
    background-color: #450a68;
}

.textarea-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

textarea {
    resize: none;
}

.add-comment-button {
    border-radius: 8px;
    font-size: 14px;
    border-color: #580c84;
    background-color: #580c84;
    color: white;
    padding: 5px;
    align-self: flex-end;
    cursor: pointer;
    margin-top: 10px;
    margin-right: 15px;
    line-height: normal;
}

.comments-section {
    display: flex;
    flex-direction: column;
}