body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f0f2f5;
}

.login-background {

    background: f0f2f5;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-container {
    width: 100%;
    height: max-content;
    max-width: 500px;
    max-height: 600px;
    background-color: #fff;
    padding: 24px;
    border-radius: 4px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin: 12px;
}

.login-background > .dark-mode-btn {
    position: absolute;
    top: 0;
    left: 20px;
}

.login-title,
.create-account-button,
.page-link {
    margin-bottom: 24px;
}

.login-title {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 24px;
    text-align: center;
}

.login-form {
    margin-bottom: 16px;
}

.error-message {
    font-weight: bold;
    color: red;

}

.login-label {
    display: block;
    font-size: 14px;
    font-weight: bold;
    color: #333;
    margin-bottom: 8px;
}

.login-input {
    display: block;
    width: 100%;
    padding: 8px;
    margin-top: 10px;
    font-size: 24px;
    line-height: 1.5;
    color: #333;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #fff;
}

.extra-content {
    font-size: 24px;
}

.side_h1 {

    color: #cf31ff;

}

.login-button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 24px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    background-color: #cf31ff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 15px;
}

.forgot-link {
    display: block;
    font-size: 14px;
    font-weight: bold;
    color: #4267b2;
    text-align: center;
    text-decoration: none;
    margin-top: 16px;
}

.create-account-container {
    text-align: center;
    margin-top: 16px;

}

.create-account-button {
    display: inline-block;
    padding: 10px 16px;
    font-size: 24px;
    color: #fff;
    background-color: #42b72a;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.page-link {
    display: block;
    font-size: 12px;
    color: #666;
    text-align: center;
    text-decoration: none;
    margin-top: 24px;
    font-weight: normal;
}

.page-link-container {
    margin-top: 24px;
}



/* Responsive adjustments */
@media (min-width: 600px) {
    .main-container {
        flex-direction: row;
        justify-content: space-around;
        align-items: flex-start;
    }

    .login-container,
    .extra-content {
        width: 45%;
        margin: 12px;
    }
}

@media (min-width: 768px) {
    .main-container {
        max-width: 700px;
    }
}

@media (min-width: 1024px) {
    .main-container {
        max-width: 960px;
    }
}


/* Dark Mode Styles */
.dark-mode {
    background-color: #181a1b;
    color: #ffffff;
}

.dark-mode .main-post {
    background-color: #282c34;
    color: #ffffff;
}


