.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(111, 105, 105, 0.5);
    /* Semi-transparent background */
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    justify-content: center;
    /* Center the modal horizontally */
    align-items: center;

}

.modal-content {
    background-color: #fff;
    padding: 20px;
    width: 400px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.headline {
    margin-bottom: 20px;
}

.form-container {
    width: 100%;
    /* Ensure the container takes full width */
    display: table-column;
    justify-content: flex-start;
    /* Align items to the left */
}

.post-button {
    width: 400px;
    height: 40px;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #fff;
    cursor: pointer;
}

textarea:focus {
    outline: none;
    /* Remove the default outline */
}

.create-post-userinfo {
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-bottom: 10px;
}

.exit-create-post-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    display: flex;
    margin-left: auto;
    border-radius: 7px;
}

.exit-create-post-button:hover {
    background-color: #f0f0f0;
}

.post-image-on-create {
    max-width: 100%;
    max-height: 300px;
    margin-top: 10px;
}
.image-input{
    margin-top: 10px;
    margin-bottom: 10px;
    border-color: transparent;
    background-color: transparent;
  

}
.photo-icon{
    width: 30px;
    height: 30px;
}
.image-input:hover{
cursor: pointer;
}
.visually-hidden {
    position: absolute;
    left: -9999px;
    top: -9999px;
  }