.menu-container {
    height: min(600px, calc(100vh - 100px));
}

.menu {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
    overflow-y: auto;
    height: 100%;

}

.side-item {
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
    padding: 5px 10px; /* Reduced padding */
    margin-bottom: 8px; /* Reduced space between items */
    font-size: 1rem; /* Smaller font size */
    display: flex; /* Ensure items align nicely */
    align-items: center; /* Center items vertically */
    gap: 10px; 
}
.side-item:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}
.item-pic {
    margin-right: 15px;
    width: 30px; 
    height: 30px; 
}

.pfp {
    border-radius: 50%;
    height: 35px;
    width: 35px;
    object-fit: cover;
    box-shadow: 0px 0px 3px 0px #000000;
    margin-left: 2px;
    margin-top: 2px;
}