.add-post-intro {
    display: flex;
    background-color: #fff;
    border: 1px solid #dddfe2;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 30px;
    width: 700px;

}

.post-avatar {
    border-radius: 50%;
    height: 40px;
    width: 40px;
    object-fit: cover;
    box-shadow: 0px 0px 3px 0px #000000;
    margin-left: 2px;
    margin-top: 2px;
    margin-right: 10px;
}

.create-post-button {
    background-color: #f0f0f0;
    border: 1px solid #dddfe2;
    border-radius: 15px;
    padding: 16px;
    width: 600px;
    height: 40px;
    display: flex;
    align-items: center;

}

.create-post-button span {
    font-family: Arial, sans-serif;
    margin-left: 10px;

}

.create-post-button {
    cursor: pointer;
}