/* register.css */
.register-background {
    background: white;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.register-main {
    background: white;
    padding: 40px;
    /* Increased padding */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 900px;
    /* Increased max-width */
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.register-textMain {
    font-size: 23px;
}

.register-textMain h1 {
    font-size: 45px;
    /* Your desired font size */
    text-align: center;
    /* Centers text horizontally */
    display: flex;
    /* Makes the container a flex container */
    justify-content: center;
    /* Centers children horizontally within the container */
    align-items: center;
    /* Centers children vertically within the container */
    height: 100px;
    /* Set a specific height for your h1 element */
    width: 100%;
    /* Ensure the width is 100% for proper centering */

}

.register-form {
    display: flex;
    flex-direction: column;
}

.register-form input {
    margin-bottom: 10px;
    padding: 15px;
    font-size: 26px;
    /* Space between inputs */
}

.register-form button {
    background-color: #ff758c;
    /* Button color */
    color: white;
    border: none;
    font-size: 16px;
    padding: 15px;
    margin-top: 10px;
    /* Space above the button */
    border-radius: 5px;
    /* Rounded corners for the button */
    cursor: pointer;
    /* Pointer cursor on hover */
}

.register-form button:hover {
    background-color: #e6676b;
    /* Slightly darker on hover */
}

.register-form img[name="img"] {
    width: 80%;
    /* Example: image width is 80% of its container */
    height: auto;
    /* Height is automatic to keep the aspect ratio */
    margin-bottom: 10px;
    /* Add space below the image */
}

.register-form .gender-selection {
    /* Your styles for the gender selection div */
    margin-bottom: 10px;
    /* Space below the gender selection */
    display: flex;
    /* To align radio buttons in a row */
    justify-content: space-around;
    /* To distribute space around elements */
    font-size: 1.5rem;

}

.register-form .error-message {

    margin-bottom: 20px;
    font-weight: bold;
    color: red;

}

.register-form .P-error-message {

    font-weight: bold;
    color: red;

}

.register-form .green-message {
    display: none;
    margin-bottom: 10px;
    font-weight: bold;
    color: rgb(59, 255, 85);

}

.gender-selection input[type="radio"] {
    /* Scale up the radio buttons. The value can be adjusted as needed. */
    transform: scale(2);
    margin-right: 10px;
    /* Add some space between the radio button and the label */
    cursor: pointer;
    /* To indicate that the element is clickable */
}