.home {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #F4FDF9;
  overflow: hidden;
  height: 100vh;
}
.wrapper{
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.container {
  display: flex;
  justify-content: space-between;
  margin-left: 2%;
  margin-right: 2%;
}
.profile-page {
  padding-right: 10px;
  width: 900px;

  overflow-y: auto;
  height: calc(110vh - 150px);
  border-radius: 10px;
}
.profile-header {
  text-align: center;
  margin-bottom: 20px;
  width: 693px;
  background-color: white;
  padding: 20px;
  align-items: center;
}

.profile-detailes {
  display: flex;
  align-items: center;
  justify-content: left;
  margin: 10px
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 20px;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: right;
  margin-bottom: 20px;
}

.user-name {
  margin: 0;


}

.friend-btn {
  padding: 10px 20px;
  cursor: pointer;
  background-color: #651195;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.update-btn {
  padding: 10px 20px;
  cursor: pointer;
  background-color: #651195;
  color: white;
  border: none;
  margin-right: 5px;
  border-radius: 5px;
  font-size: 16px;
}

.friend-btn:hover {
  background-color: #52087e;
}

.display-btn {
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  font-size: 18px;
  flex-grow: 1;
  margin: 5px;
}

.display-btn.active {
  color: #7e30ab;
  font-weight: bold;
}

.display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.dark-mode .profile-header {
  background-color: #232526;
  color: #ffffff;
}

.dark-mode .display-btn {
  background-color: #232526;
  color: #ffffff;
}


.dark-mode .active {
  background-color: #232526;
  color: #b6309b;
}
.dark-mode .profile-page-friends-list {
  background-color: #232526;
}
.profile-page-friends-list {
  background-color: white;
  /* Add border */
  border-radius: 10px;
  /* Add border radius for rounded corners */
  width: 693px;
  background-color: white;
  padding: 20px;
}



.update-popup {
  border-radius: 10px;
  background-color: #F0F2F5;
  /* 1px solid black border */
  margin: 20px;
  width: 300px;
  /* 20px margin on all sides */
  display: flex; /* Use flexbox to arrange items */
  flex-direction: column;

}

.update-popup-update-btn {
  
  margin:10px;
  margin-right: 10px;
  font-size: 15px;
}
.update-popup-file-input{
  margin:10px;
 
  font-size: 14px;

}
.update-popup-close-btn {

  margin-right: 10px;
  margin-left:10px;
  margin-bottom: 10px;
  font-size: 15px;
}

.update-popup-input {
 margin:10px;
  width: 150px;
  font-size: 14px;
  /* 20px margin on all sides */
  font-size: large;

}
.update-popup-input::placeholder {
  font-size: 14px; /* Set the placeholder text size */
  /* Add other styles as needed */
}
.dark-mode .wrapper {
  background-color: #333738;
  color: #ffffff;
  height: auto;
  min-height: 100vh;
}
.profile-page{
  height: 100vh;
}
